import React from 'react';

type CountProps = {
  value: number;
};

export const Count: React.FC<CountProps> = React.memo(({ value }) => {
  if (value > 1000000000) {
    return <>{(value / 100000000).toFixed()}</>;
  } else if (value > 100000000) {
    return <>{(value / 100000000).toFixed(1)}</>;
  } else if (value > 100000) {
    return <>{(value / 10000).toFixed()}</>;
  } else if (value > 10000) {
    return <>{(value / 10000).toFixed(1)}</>;
  }
  return <>{value}</>;
});
