import { useCallback, useLayoutEffect, useState } from 'react';
import ResizeObserver from 'resize-observer-polyfill';

export interface Size {
  width: number;
  height: number;
}

/**
 * 
 */
export const useWindowSize = (type: 'inner' | 'client' = 'client'): Size => {
  const [size, setSize] = useState<Size>(
    type === 'inner'
      ? {
          width: window.innerWidth,
          height: window.innerHeight,
        }
      : {
          width: document.body.clientWidth,
          height: document.body.clientHeight,
        },
  );

  const handleResize = useCallback(() => {
    setTimeout(() => {
      setSize(
        type === 'inner'
          ? {
              width: window.innerWidth,
              height: window.innerHeight,
            }
          : {
              width: document.body.clientWidth,
              height: document.body.clientHeight,
            },
      );
    }, 50);
  }, [type]);

  useLayoutEffect(() => {
    window.addEventListener('resize', handleResize);

    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, [handleResize]);

  return size;
};

/**
 * 
 */
export const useWindowSizeEffect = (callback: (size: Size) => void): void => {
  const size = useWindowSize();
  callback(size);
};

/**
 * 
 */
export const useElementSize = (element: React.RefObject<HTMLElement>): Size => {
  const [size, setSize] = useState<Size>({
    width: 0,
    height: 0,
  });

  useLayoutEffect(() => {
    const observer = new ResizeObserver((entries: ResizeObserverEntry[]) => {
      const width = entries[0].contentRect.width;
      const height = entries[0].contentRect.height;
      setSize({ width, height });
    });

    if (element.current) {
      observer.observe(element.current);
    }

    return () => observer.disconnect();
  }, [element]);

  return size;
};

/**
 * 
 */
export const useElementSizeEffect = (element: React.RefObject<HTMLElement>, callback: (size: Size) => void): void => {
  const size = useElementSize(element);
  callback(size);
};
